.row{
    color:white;
    margin-left:20px;
}

.row_posters{
    display:flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar{
    display:none;
}
.row_poster{
    max-height: 100px;
    object-fit:contain;
    margin-right:10px;
    transition: transform 450ms;
    border-radius:5px;
   
    
}

.row_poster:hover{
    transform:scale(1.08);
    opacity: 1;
}
.row_posterlarge{
    max-height:250px;
    border-radius:7px;
  

}
.row_posterlarge:hover{
    transform:scale(1.09);
    opacity:1;
    
}